import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  cnpjFormatter,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { CLASS_NAME, Sindicato } from '../../../../models/dtos/mfp/sindicato';
import { CLASS_NAME as CLASS_NAME_EXAMPLE } from '../../../../models/dtos/mfp/sindicatoExample';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'codigo',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const SindicatoSearchView: FC<SearchViewProps<Sindicato>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'razaoSocial',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Razão Social',
    },
    {
      dataField: 'cnpj',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      formatter: cnpjFormatter,
      sort: true,
      text: 'C.N.P.J.',
    },
    {
      dataField: 'codigoEntidadeSindical',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Cód. Entidade Sindical',
    },
  ];

  return (
    <WindowComponent
      title="Manutenção de Sindicato"
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME_EXAMPLE}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'SindicatoEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default SindicatoSearchView;
